import styled from "styled-components";
import Router, { withRouter } from "next/router";
import { Header, Inline, Logo, User } from "../WebHead/web-head-css.js";

const HeaderWork = styled(Header)`
  background: #f2f7fa;
  border: 1px solid #e9e8e7;
`;

const handleClick = () => {
  Router.push("/");
};

const WebHeadForWorkPage = () => {
  return (
    <HeaderWork>
      <Inline>
        <Logo onClick={handleClick}>Practice</Logo>
        <User />
      </Inline>
    </HeaderWork>
  );
};

export default withRouter(WebHeadForWorkPage);
